.deal {
  padding-bottom: 85px;
  &__inner {}
  &__title {
    margin-bottom: 48px;
  }
  &__row {
    display: flex;
    justify-content: space-between;
  }
  &__item {
    flex-basis: calc(33.333% - 21px);
  }
}

.deal-card {
  &__img {
    margin-bottom: 36px;
  }
  &__title {
    font-family: $ff-secondary;
    font-size: 24px;
    line-height: 2.1;
    text-transform: uppercase;
    &::first-letter {
      font-size: 36px;
    }
  }
  &__list {

  }
  &__list-item {
    font-weight: 300;
    font-size: 18px;
    line-height: 1.5;
    &::before {
      content: '';
      display: inline-block;
      border-radius: 50%;
      width: 4px;
      height: 4px;
      background-color: #000000;
      vertical-align: middle;
      margin-right: 15px;
    }
  }
}


@media (min-width: 1200px) {

}
@media (min-width: 992px) and (max-width: 1199px) {

}
@media (min-width: 768px) and (max-width: 900px) {
  .deal {
    padding-top: 80px;
    &__row {
      flex-wrap: wrap;
    }
    &__item {
      flex-basis: 47%;
      flex-grow: 0;
      flex-shrink: 0;
      margin-bottom: 35px;
      &:last-child {
        flex-basis: 50%;
        margin: 0 auto;
      }
    }
  }
  .deal-card {
    &__img {
      margin-bottom: 0;
    }
  }
}
@media (min-width: 768px) and (max-width: 991px) {

}
@media (max-width: 767px) {
  .deal {
    padding-top: 70px;
    &__row {
      flex-direction: column;
      align-items: center;
    }
    &__item {
      // margin-bottom: 21.875vmin;
      margin-bottom: 50px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  .deal-card {
    &__img {
      margin-bottom: 0;
    }
  }
}
@media (max-width: 480px) {
  .deal {
    padding-top: 21.875vmin;
    padding-bottom: 23vmin;
    &__title {
      margin-bottom: 7.8vmin;
    }
    // &__row {
    //   flex-direction: column;
    // }
    &__item {
      margin-bottom: 21.875vmin;
    //   &:last-child {
    //     margin-bottom: 0;
    //   }
    }
  }
  .deal-card {
    &__img {
      margin-bottom: 5vmin;
    }
    &__title {
      font-size: 5.625vmin;
      line-height: 2.8;
      &::first-letter {
