.about {
  padding-top: 60px;
  &__inner {
    background-repeat: no-repeat;
    background-position: right bottom;
    background-size: auto;
    background-image: url(../img/bg/bg3.png);
  }
  &__title {
    margin-bottom: 38px;
  }
  &__content {
    display: flex;
    justify-content: flex-start;

  }
  &__img {
    margin-top: auto;
    margin-bottom: 0;
  }
  &__text {
    flex-grow: 1;
    padding-left: 70px;
    padding-top: 50px;
  }
  &__list {
    margin-bottom: 63px;
  }
  &__list-item {
    font-weight: 300;
    font-size: 15px;
    line-height: 1.8;
    text-transform: uppercase;
    margin-bottom: 23px;
    &:last-child {
      margin-bottom: 0;
    }
    position: relative;
    padding-left: 40px;
    &::before {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      width: 22px;
      height: 27px;
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
    }
    &:nth-of-type(1)::before {
      background-image: url(../img/icon/geo-blue.svg);
    }
    &:nth-of-type(2)::before {
      background-image: url(../img/icon/arrow-blue.svg);
    }
    &:nth-of-type(3)::before {
      background-image: url(../img/icon/calendar-blue.svg);
    }
  }
  &__address {
    padding-left: 40px;
    font-weight: 300;
    font-size: 16px;
    line-height: 1.7;
  }
}



