@media (max-width: 480px) {
  .about {
    padding-top: 3vmin;
    padding-bottom: 6.25vmin;
    &__inner {
      background-image: url(../img/bg/bg3-blue.png);
      background-position: center top;
      background-size: contain;
      padding-top: 80vmin;
    }
    &__title {
      margin-bottom: 11.875vmin;
    }
    &__content {
      flex-direction: column;
    }
    &__img {
      margin-bottom: 3.125vmin;
      margin-left: -15px;
      width: calc(100% + 30px);
    }
    &__text {
      padding-left: 0;
      order: -1;
      padding-top: 0;
    }
    &__list {

    }
    &__list-item {
      font-size: 4.6875vmin;
      margin-bottom: 9vmin;
      padding-left: 12vmin;
      &::before {
        top: 0;
        width: 6.875vmin;
        height: 12.5vmin;
      }
    }
    &__address {
      display: none;
    }
    &__address-mob {
      // padding-left: 40px;
      text-align: center;
      font-weight: 300;
      font-size: 4.0625vmin;
      line-height: 2;
    }
  }
}
