.expensive {
  position: relative;
  z-index: 1;
  padding: 33px 0 180px;
  overflow: hidden;
  &::before {
    content: '';
    display: block;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    filter: blur(2px);
    background-image: url(../img/bg/bg2.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    transform: scale(1.01);  //Решение для рамок по краям
  }
  &__circle {
    background-color: rgba(255, 255, 255, 0.5);
    width: 100%;
    height: 170%;
    position: absolute;
    z-index: -1;
    border-radius: 50%;
    left: 50%;
    top: 0;
    transform: translate(-50%, -50%);
    &::before, &::after {
      content: '';
      display: block;
      border-radius: 50%;
      background-color: rgba(255, 255, 255, 0.5);
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
    &::before {
      // width: 108.5%;
      // height: 110%;
      width: 93%;
      height: 93%;
    }
    &::after {
      width: 86%;
      height: 86%;
      // width: 116%;
      // height: 120%;
    }
  }
  &__inner {

  }
  &__content {
    display: flex;
    align-items: center;
  }
  &__title {
    margin-bottom: 58px;
  }
  &__list {
    margin: 0 auto;
  }
  &__item {
    font-weight: 300;
    font-size: 15px;
    line-height: 1.8;
    text-transform: uppercase;
    margin-bottom: 20px;
    position: relative;
    padding-left: 38px;
    &:last-child {
      margin-bottom: 0;
    }
    &::before {
      content: '';
      display: block;
      width: 22px;
      height: 40px;
      position: absolute;
      left: 0;
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      top: 50%;
      transform: translateY(-50%);
    }
    &:nth-of-type(1)::before {
      background-image: url(../img/icon/dollar-blue.svg);
    }
    &:nth-of-type(2)::before {
      background-image: url(../img/icon/coins-blue.svg);
    }
  }
}



