@media (min-width: 480px) and (max-width: 1024px) {
  .expensive {
    &__content {}
    &__list {
      width: 65%;
    }
  }
}
@media (min-width: 768px) and (max-width: 991px) {

}
@media (max-width: 767px) {
  .expensive {
    &__circle {
      width: 170%;
      height: 180%;
      &::before {}
      &::after {
        display: none;
      }
    }
  }
}
@media (max-width: 480px) {
  .expensive {
    padding: 15vmin 0 40vmin;
    &__circle {
      width: 220%;
      height: 180%;
      &::before {}
      &::after {
        display: none;
      }
    }
    &__title {
      margin-bottom: 13vmin;
    }
    &__item {
      font-size: 4.6875vmin;
      margin-bottom: 9vmin;
      padding-left: 12vmin;
      &::before {
        top: 1.2em;
        width: 6.875vmin;
        height: 12.5vmin;
      }
    }
  }
}
