body {
  font-family: $ff-main;
}

.container {
  max-width: 1406px;
  padding-left: 15px;
  padding-right: 15px;
  width: 100%;
  margin: 0 auto;
}

.logo {
  display: flex;
  &__img {
    max-width: 90px;
  }
  &__text {
    font-size: 12px;
    line-height: 12px;
    max-width: 225px;
    border-left: 1px solid #000000;
    padding-left: 10px;
    margin-left: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}


.section-title {
  font-family: $ff-secondary;
  font-weight: 600;
  font-size: 36px;
  line-height: 1.44;
  text-align: center;
  text-transform: uppercase;
  // margin-bottom: 48px;
}




