.header {
  padding: 10px 80px 13px 32px;
  position: absolute;
  z-index: 5;
  width: 100%;
  left: 0;
  top: 0;
  right: 0;
  // background: rgba(255, 255, 255, 0.8);
  background-color: #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo {
    & .logo__img {
      // max-width: 90px;
    }
  }
  &__contacts {
    display: flex;
    align-items: center;
  }
  &__geo {
    // max-width: 120px;
    margin-right: 40px;
    font-size: 13px;
    line-height: 12px;
    position: relative;
    &::before {
      content: '';
      display: block;
      position: absolute;
      left: -30px;
      top: -3px;
      width: 15px;
      height: 18px;
      background: no-repeat center / contain;
      background-image: url(../img/icon/geo.svg);
    }
  }
  &__tel {
    font-size: 18px;
    font-weight: 700;
    border-bottom: 2px dotted #000000;
    display: inline-block;
    padding-bottom: 3px;
    &:hover {
      border-bottom-style: solid;
    }
  }
}





