.buy-list {
  padding: 60px 0 100px;
  background-color: $color-accent;
  &__inner {

  }
  &__title {
    margin-bottom: 55px;
    color: #ffffff;
  }
  &__row {
    counter-reset: buyList 0;
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
  &__item {
    padding-top: 60px;
    padding-left: 15px;
    position: relative;
    z-index: 1;
    font-family: $ff-secondary;
    color: #ffffff;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.5;
    text-transform: uppercase;
    &:nth-of-type(5) {
      line-height: 1;
    }
    & > span {
      font-size: 10px;
      line-height: 1.6;
      font-weight: 400;
    }
    &::before {
      content: '';
      display: block;
      height: 2px;
      width: 60px;
      background-color: #ffffff;
      position: absolute;
      top: 0;
      left: 0;
    }
    &::after {
      content: "0" counter(buyList) ".";
      counter-increment: buyList;
      color: #2883C2;
      font-weight: 700;
      font-size: 120px;
      line-height: 0.22;
      font-family: $ff-main;
      position: absolute;
      left: 0;
      top: 0.5em;
      z-index: -1;
    }
  }
}


