@media (min-width: 992px) and (max-width: 1199px) {

}
@media (min-width: 481px) and (max-width: 1024px) {
  .buy-list {
    padding-bottom: 35px;
    &__row {
      flex-wrap: wrap;
      justify-content: space-around;
    }
    &__item {
      flex-shrink: 1;
      flex-grow: 0;
      margin-bottom: 60px;
    }
  }
}
@media (min-width: 768px) and (max-width: 991px) {

}
@media (max-width: 767px) {

}
@media (max-width: 480px) {
  .buy-list {
    padding-top: 9.375vmin;
    padding-bottom: 21.875vmin;
    &__title {
      margin-bottom: 15vmin;
    }
    &__row {
      flex-direction: column;
      padding-left: 5vmin;
    }
    &__item {
      font-size: 5.625vmin;
      padding-top: 18.75vmin;
      padding-left: 13vmin;
      padding-bottom: 12vmin;
      margin-bottom: 18vmin;
      &:last-child {
        margin-bottom: 0;
      }
      &::before {
        width: 9.375vmin;
      }
      &::after {
        font-size: 37.5vmin;
      }
      & > span {
        font-size: 3.125vmin;
      }
    }
  }
}
