.footer {
  padding: 17px 0;
  background-color: #fcfcfc;
  box-shadow: 0px -4px 4px rgba(0, 0, 0, 0.05);
  &__row {
    display: flex;
    justify-content: space-between;
  }
  &__box {
    text-align: center;
    font-weight: 300;
    font-size: 12px;
    line-height: 2.25;
    &:nth-of-type(2) {
      flex-grow: 1;
    }
    & a {
      display: inline-block;
      border-bottom: 1px solid #000;
    }
  }
}


