@media (max-width: 600px) {
  .footer {
    &__row {
      flex-direction: column;
      align-items: center;
    }
  }
}
@media (max-width: 480px) {
  .footer {
    padding: 6.25vmin 0 8.4vmin;
    // &__row {
    //   flex-direction: column;
    //   align-items: center;
    // }
    &__box {
      font-size: 3.125vmin;
      line-height: 2.7;
      margin-bottom: 1.8vmin;
      &:last-child {
        margin-bottom: 0;
      }
      & a {
        display: inline;
      }
    }
  }
}
