.safe {
  padding: 78px 0 97px;
  // padding-right: 85px;
  position: relative;
  &__inner {}
  &__title {
    margin-bottom: 50px;
  }
  &__content {
    display: flex;
    justify-content: flex-end;
  }
  &__img {  //TODO поискать другое решение и решить вопрос с отступами
    flex-basis: 50%;
    // flex-shrink: 0;
    // position: absolute;
    // width: 50%;
    // left: 0;
    // top: 0;
    // margin-left: ;
  }
  &__text {
    padding-right: 85px;
    flex-basis: 50%;
    padding-left: 25px;
    font-size: 18px;
    line-height: 1.5;
    font-weight: 300;
    & p {
      margin-bottom: 65px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__bubble {
    padding: 17px 47px 15px 23px;
    margin-left: 50px;
    margin-bottom: 50px;
    background-color: $color-accent;
    color: #ffffff;
    font-weight: 700;
    font-style: italic;
    display: inline-block;
    position: relative;
    z-index: 1;
    &::before {
      content: '';
      display: block;
      background-color: $color-accent;
      width: 40px;
      height: 40px;
      position: absolute;
      z-index: -1;
      left: 25px;
      top: -8px;
      transform: scaleX(1.2) rotate(45deg) ;
    }
  }
}


@media (min-width: 1025px) and (max-width: 1300px) {
  .safe {
    &__text {
      padding-right: 0;
    }
  }
}
@media (max-width: 1024px) {
  .safe {
    &__text {
      padding-left: 30px;
      padding-right: 30px;
      order: -1;
      margin-bottom: 30px;
    }
    &__content {
      flex-direction: column;
    }
    &__img {
      flex-basis: 100%;
      // margin-bottom: 30px;
      & > img {
        max-width: 800px;
        width: 100%;
        margin: 0 auto;
      }
    }
  }
}
