@media (max-width: 480px) {
  .safe {
    padding: 21vmin 0 9vmin;
    &__title {
      margin-bottom: 9.375vmin;
    }
    &__content {
      // flex-direction: column;
    }
    &__img {
      // position: static;
      // flex-basis: 100%;
      // width: 100%;
      margin-left: -15px;
      // flex-basis: calc(100% + 30px);
      width: calc(100% + 30px);
    }
    &__text {
      // order: -1;
      font-size: 4.06vmin;
      padding-right: 0;
      padding-left: 0;
      margin-bottom: 10vmin;
      & p {
        margin-bottom: 10vmin;
        padding-right: 7vmin;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
    &__bubble {
      padding: 5vmin 11vmin 4.5vmin 7vmin;
