$arrows-space: 60px;
$header-pad: 55px;
.top {  //TODO решить вопрос с местом под стрелки
  // margin-top: $header-pad;
  // padding-top: $header-pad;
  min-height: 100vh;
  position: relative;
  overflow: hidden;
  margin-bottom: $arrows-space;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  &::after {
    content: '';
    display: block;
    position: absolute;
    z-index: -1;
    top: $header-pad;
    left: 0;
    right: 0;
    bottom: 0;
    filter: blur(2px);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    transform: scale(1.005);  //Решение для рамок по краям
  }
  &__circle {
    background-color: rgba(255, 255, 255, 0.5);
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    width: 81.5%;
    height: 60%;
    &::before, &::after {
      content: '';
      display: block;
      position: absolute;
      z-index: -1;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      border-radius: 50%;
      background-color: rgba(255, 255, 255, 0.5);
    }
    &::before {
      width: 92%;
      height: 91%;
    }
    &::after {
      width: 85%;
      height: 82%;
    }
  }
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    height: 100%;
    margin: 0 auto;
    position: relative;
    z-index: 1;
    max-width: 740px;
  }
  &__title {
    font-family: $ff-secondary;
    font-weight: 600;
    font-size: 44px;
    line-height: 1.18;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 30px;
  }
  &__subtitle {
    font-weight: 300;
    font-size: 15px;
    line-height: 1.8;
    text-align: center;
    text-transform: uppercase;
  }
  &__arrows {
    background-color: #ffffff;
    width: 100%;
    height: 60px;
    position: absolute;
    bottom: -2px;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: flex-end;

  }
  &__arrow {
    display: flex;
    margin-bottom: 15px;
    &:nth-of-type(1) {
      animation: animArrow1 1.5s linear infinite;
    }
    &:nth-of-type(2) {
      animation: animArrow2 1.5s linear infinite;
    }
    &::before, &::after {
      content: '';
      display: block;
      background-color: $color-accent;
      height: 5px;
      width: 55px;
    }
    &::before {
      transform: rotate(20deg) translateX(2.5px);
    }
    &::after {
      transform: rotate(-20deg) translateX(-2.5px);
    }
  }
}
@keyframes animArrow1 {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 1;
  }
  50% {
    opacity: 1;
  }
  75% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes animArrow2 {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  75% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}


